import { Component } from '@angular/core';
import { NavController,ToastController } from 'ionic-angular';
import {Login} from '../../providers/login';
import {MapPage} from '../map-page/map-page';
import {RealTimeService} from '../../providers/real-time.service';
import {HistoricalService} from '../../providers/historical.service';
import { DataManagementService } from '../../providers/data-management.service';

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {

  login: String = null;
  mdp: String = null;

  constructor(private historicalService: HistoricalService,private realTimeService: RealTimeService,private dataManagementService: DataManagementService, public navCtrl: NavController,private signinService: Login,private toastController: ToastController) {

  }

  onSubmit() {
    this.signinService.login({"username":this.login,"password":this.mdp}).subscribe(
      token => {
          this.goToRt();
          this.signinService.token = token.token;
          this.realTimeService.token = token.token;
          this.historicalService.token = token.token;
          this.dataManagementService.token = token.token;
      }
      , ()=> {
        let toast = this.toastController.create({
          message: 'Authentification Echoue!',
          duration: 2000
        });
        toast.present();
      });
  }

  goToRt(){
    this.navCtrl.push(MapPage, {
      value: null
    });
  }

}



