import { Component } from '@angular/core';
import { NavController, NavParams, ModalController, LoadingController } from 'ionic-angular';
import { DatePipe } from '@angular/common';
import { MapService } from "../../utils/map.service";
import { HistoricalService } from "../../providers/historical.service";
import { DataManagementService } from "../../providers/data-management.service";
import { HistoricalForm } from '../historical-form/historical-form';
import { PathsList } from '../paths-list/paths-list';
import { Subscription } from "rxjs";
import { imagesDir } from "../../providers/global.config";
import Marker = L.Marker;
import Icon = L.Icon;
import 'leaflet';


@Component({
  selector: 'page-historical',
  templateUrl: 'historical.html',
})
export class Historical {

  allPaths: Subscription;
  paths: any[];
  loader: any;

  constructor(private dataManagementService: DataManagementService, private historicalService: HistoricalService, public loadingCtrl: LoadingController, public modalCtrl: ModalController, public navCtrl: NavController, public navParams: NavParams, public mapService: MapService) {
  }

  ionViewWillEnter() {
    this.init();
  }

  init() {
    this.initMap();
   // this.openFormModal();
  }

  initMap() {
    if (this.mapService.map) this.mapService.map.remove();
    let map = L.map('historicalMap', {
      zoomControl: false,
      center: L.latLng(32.586163, -9.912118),
      zoom: 6,
      minZoom: 3,
      maxZoom: 20,
      maxNativeZoom: 17,
      layers: [this.mapService.baseMaps.RimTelecom]
    });
    L.control.zoom({ position: 'topright' }).addTo(map);
    this.mapService.map = map;
  }

  openFormModal() {
    let formModal = this.modalCtrl.create(HistoricalForm);
    formModal.present();
    formModal.onDidDismiss(form => {
      if (form != null) {
      //let firstDateStr = form.firstDate.split("-");
      //let firstDate = firstDateStr[2] + "/" + firstDateStr[1] + "/" + firstDateStr[0];
      let firstDateTime = form.firstDate.getTime();
      //let lastDateStr = form.lastDate.split("-");
     // let lastDate = lastDateStr[2] + "/" + lastDateStr[1] + "/" + lastDateStr[0];
      let lastDateTime =form.lastDate.getTime();
      this.loader = this.loadingCtrl.create({
        content: "Veuillez attendre..."
      });
      this.loader.present();
      this.allPaths = this.historicalService.getAllPaths(form.selectedDeviceId, {
        startDate: firstDateTime,
        endDate: lastDateTime
      }).subscribe(paths => {
        this.paths = paths;
        if(this.paths != null)
        this.paths.forEach(path => {

          var date = new Date(path.beginPathTime-3600000);
          var hours = date.getHours();
          var minutes = "0" + date.getMinutes();
          path.displayBeginPathTime = hours + ':' + minutes.substr(-2);

          date = new Date(path.endPathTime-3600000);
          hours = date.getHours();
          minutes = "0" + date.getMinutes();
          path.displayEndPathTime = hours + ':' + minutes.substr(-2);

          path.beginPathGeocodingDetails = this.dataManagementService.getRelativePosition(path.beginPathLatitude, path.beginPathLongitude);
          path.beginPathGeocoding = path.beginPathGeocodingDetails;
          if (path.beginPathGeocodingDetails == null) {
            this.dataManagementService.inverseGeoconding(path.beginPathLatitude, path.beginPathLongitude, 17).subscribe(adress => {
              path.beginPathGeocoding = this.processGeocoding(adress.display_name);
              if (adress.address.road != null && adress.address.neighbsourhood != null && adress.address.city != null) {
                path.beginPathGeocoding = adress.address.road + ' ' + adress.address.neighbourhood + '(' + adress.address.city + ')';
                path.beginPathGeocodingDetails = adress.address.road + ' ' + adress.address.neighbourhood + '(' + adress.address.city + ')';
              } 
              /*else {
                let truncatedDisplayName = adress.display_name;
                let countCommas = 0;
                for (var i = 0, len = adress.display_name.length; i < len; i++) {
                  if (adress.display_name[i] == ',') {
                    countCommas = countCommas + 1;
                  } else if (countCommas >= 4) {
                    truncatedDisplayName = adress.display_name.substring(0, i - 1);
                    path.beginPathGeocoding = truncatedDisplayName;
                    path.beginPathGeocodingDetails = path.beginPathGeocoding;
                    break;
                  }
                }
              }*/
            }, (err) => {
              path.beginPathGeocodingDetails = null;
              path.beginPathGeocoding = null;
            });
          }
          path.endPathGeocodingDetails = this.dataManagementService.getRelativePosition(path.endPathLatitude, path.endPathLongitude);
          path.endPathGeocoding = path.endPathGeocodingDetails;
          if (path.endPathGeocodingDetails == null) {
            this.dataManagementService.inverseGeoconding(path.endPathLatitude, path.endPathLongitude, 17).subscribe(adress => {
              path.endPathGeocoding = this.processGeocoding(adress.display_name);
              if (adress.address.road != null && adress.address.neighbsourhood != null && adress.address.city != null) {
                path.endPathGeocoding = adress.address.road + ' ' + adress.address.neighbourhood + '(' + adress.address.city + ')';
                path.endPathGeocodingDetails = path.endPathGeocoding;
              } 
              /*else {
                let truncatedDisplayName = adress.display_name;
                let countCommas = 0;
                for (var i = 0, len = adress.display_name.length; i < len; i++) {
                  if (adress.display_name[i] == ',') {
                    countCommas = countCommas + 1;
                  } else if (countCommas >= 5) {
                    truncatedDisplayName = adress.display_name.substring(0, i - 1);
                    path.endPathGeocoding = truncatedDisplayName;
                    path.endPathGeocodingDetails = path.endPathGeocoding;
                    break;
                  }
                }
              }*/
            }, (err) => {
              path.endPathGeocodingDetails = null;
              path.endPathGeocoding = null;
            });
          }
        
        });
        this.openResultsModal();
        this.loader.dismiss();
      });
    }
    });
  }

  openResultsModal() {
    let pathsListModal = this.modalCtrl.create(PathsList, { paths: this.paths });
    pathsListModal.present();
    pathsListModal.onDidDismiss(pathClicked => {
      if (pathClicked != null)
      this.drawPath(pathClicked);
    });
  }

  drawPath(path: any) {
    this.mapService.removePolylinesFromMap();
    this.mapService.removeMarkersFromMap();
    this.historicalService.getPathDetails(path.deviceId, {
      startDate: path.beginPathTime,
      endDate: path.endPathTime
    }).subscribe(points => {
      let stopMarkers: Marker[] = [];
      points.stops.forEach(stop => {
        let popup = "<img src='" + imagesDir + "stop_smal.png" + "'/> Dure d'arrt : " + stop.stopDurationStr;
        /*let stopMarker = new Marker({
          lat: stop.stopLatitude,
          lng: stop.stopLongitude
        });*/
        let stopMarker = L.marker({lat : stop.stopLatitude, lng: stop.stopLongitude});
        stopMarker.addTo(this.mapService.map);
        stopMarker.on('click', () => {this.mapService.map.setView({lat : stop.stopLatitude, lng: stop.stopLongitude}, 14);});
        this.dataManagementService.inverseGeoconding(stop.stopLatitude, stop.stopLongitude, 18).subscribe(adress => {
          let truncatedDisplayName = adress.display_name;
          let countCommas = 0;
          for (var i = 0, len = adress.display_name.length; i < len; i++) {
            if (adress.display_name[i] == ',') {
              countCommas = countCommas + 1;
            } else if (countCommas >= 5) {
              truncatedDisplayName = adress.display_name.substring(0, i - 1);
              break;
            }
          }
          popup = popup + '<br><hr><b>' + truncatedDisplayName + '</b>';
          stopMarker.bindPopup(popup);
        });
        stopMarker.setIcon(new Icon({
          iconUrl: imagesDir + "stop_smal.png",
          iconAnchor: [-2, 30],
          popupAnchor: [10, -25]
        }));
        this.mapService.addMarker(stopMarker);
      });
      let polyline = L.polyline(points.coordinates, { color: '#0031D9', weight: 3 });
      points.coordinates.forEach((coordinate, i) => {
        let marker = L.marker(coordinate);
        console.log(new Date(points.coordinates[0].date));
        if (i != 0 && i != points.coordinates.length - 1) {
          marker.setIcon(new Icon({
            iconUrl: imagesDir + "green-point.png",
            iconAnchor: [2, 2]
          }));
          var pointDatePipe = new DatePipe(coordinate.date);
          let popup = '<b>Heure:</b> ' + pointDatePipe.transform(coordinate.date, 'dd/MM/yyyy HH:mm:ss') + '<b><br>vitesse:</b> ' + coordinate.speed + ' Km/h';
          marker.bindPopup(popup);
          marker.on('click', () => {
            this.mapService.map.setView(coordinate, 17);
          });
          this.mapService.addMarker(marker);
        }
      });
      let startMarker = L.marker({ lat: path.beginPathLatitude, lng: path.beginPathLongitude });
      let startTime: any = path.beginPathTime;
      var startDatePipe = new DatePipe(startTime);
      startMarker.bindPopup('<b> Lieu de dbut: </b>' + path.beginPathGeocoding +'<b></i><br>Temps de dbut du trajet : </b>' + startDatePipe.transform(startTime, 'dd/MM/yyyy HH:mm:ss'));
      startMarker.setIcon(new Icon({
        iconUrl: imagesDir + "startMarker.png",
        iconAnchor: [-2, 30],
        popupAnchor: [10, -25]
      }));
      this.mapService.addMarker(startMarker);
      if (path.endPathLatitude != null && path.endPathLongitude != null && path.endPathTime != null) {
        var endMarker = L.marker({ lat: path.endPathLatitude, lng: path.endPathLongitude });
        let endTime: any = path.endPathTime;
        var endDatePipe = new DatePipe(endTime);
        endMarker.bindPopup('<b> Lieu de fin: </b>' + path.endPathGeocoding +'<br><b>Temps de fin du trajet : </b>' + endDatePipe.transform(endTime, 'dd/MM/yyyy HH:mm:ss')
                    + '<br><b> Dure du trajet : </b>' + path.pathDurationStr + "<br><b> Dure d'arrt : </b>" + path.nextStopDurationStr + "<br><b> Vitesse maximum : </b>"
                    + path.maxSpeed + " Km/h <br><b> Kilometrage parcouru : </b>" + path.distanceDriven.toFixed(2) + ' Km'
);
        endMarker.setIcon(new Icon({
          iconUrl: imagesDir + "endMarker.png",
          iconAnchor: [-2, 30],
          popupAnchor: [10, -25]
        }));
        this.mapService.addMarker(endMarker);
      }
      this.mapService.addPolyline(polyline);
      var middle = points.coordinates[Math.round((points.coordinates.length - 1) / 2)];
      this.mapService.map.setView(middle, 12);
    });
  }

  ionViewDidLoad() {
    this.openFormModal();
  }

  processGeocoding(geocoding: string): string{
    if (geocoding != null){
      let array = geocoding.split(',',3);
      let smallerGeocoding = '';
      array.forEach(word => {
        smallerGeocoding = smallerGeocoding + ', ' + word;
      });
      smallerGeocoding = smallerGeocoding.slice(2,smallerGeocoding.length);      
      return smallerGeocoding;
    }else 
    return null; 
  }

}



